}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 56px;
  height: 56px;
  border: 3px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

