.body {
  box-sizing: border-box;
  padding: 20px;
}

img {
  display: block;
  max-width: 100%;
  border: 1px solid #105276;
}

.is-hidden {
  display: block;
  opacity: 0;
}

.cat-info {
  margin-top: 20px;
  padding: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 20px;
}

.breed-select {
  margin: auto;
  max-width: 300px;
}

CSS Copy .loader {
  width: 48px;
  height: 48px;
  border: 3px solid #fff;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.loader::after {
  content: '';
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid;
  border-color: #ff3d00 transparent;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
